.App {
  text-align: center;
}

.content {
  position: absolute;
  margin-top: 60px;
  padding-bottom: 40px;
  background: rgba(200,200,200,0.1);
  min-height: calc(100vh - 60px);
  width: 100vw;
}

.arrow-container {
  position: fixed;
  bottom: 20px;
  width: 100vw;
}

.arrow-from,
.arrow-to {
  width: 80px;
  opacity: 0.8;
  position: absolute;
  bottom: 10px;
}

.arrow-from:hover,
.arrow-to:hover {
  opacity: 1;
  width: 95px;
}

.arrow-from {
  left: 10px;
}

.arrow-from img {
  transform: rotateZ(180deg);
}

.arrow-to {
  right: 10px;
}

.arrow-name {
  font-weight: bold;
  font-family: Roboto;
}

.footer {
  position: fixed;
  bottom: 5px;
  right: 10px;
  font-size: 0.7rem;
  font-style: italic;
  text-align: right;
}

.footer > div {
  margin-top: 10px;
  width: auto;
  background: white;
}

.nav {
  position: fixed;
  top: 0;
  width: 100vw;
  height: 60px;
  background: black;
  z-index: 99;
}

.nav-flex {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  height: 100%;
  font-size: 1.2rem;
}

.nav-class {
  color: white;
  height: 100%;
  position: relative;
  display: inline-block;
  width: 170px;
  cursor: pointer;
}

.nav-dropdown-content {
  display: none;
  position: absolute;
  background-color: black;
  width: 100px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
  width: 170px;
  font-size: 1rem;
}

.nav-dropdown-content a {
  font-size: 0.98rem;
  color: white;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}

.nav-dropdown-content a:hover {background-color:grey;}

.nav-class:hover .nav-dropdown-content {display: block;}

.nav-class:hover p {color: red;}

.task-interactive {
  margin: 20px 0;
  width: 100vw;
}

.task-btn {
  margin: 10px;
}

.task-btn button {
  height: 35px;
  width: 100px;
  font-size: 1.3rem;
  font-weight: bold;
}

.task-task {
  background: rgba(215, 183, 172, 0.5);
  width: 75%;
  margin: 15px auto;
  min-height: 20px;
}

.task-task > div{
  padding: 10px 50px;
}

.task-name {
  font-weight: bold;
  font-size: 1.5rem;
}

.task-desc {
  font-size: 1.2rem;
}

.task-title {
  font-size: 1.5rem;
  padding: 0 30px;
}

.task-img img{
  height: 150px;
}

.task-content {
  padding: 0 110px;
}

.page-title {
  margin: 30px 30px 10px 30px;
  font-size: 2rem;
  font-weight: bold;
  font-family: Roboto;
}

.page-content {
  margin: 10px 50px;
}

.page-text {
  margin: 15px 50px;
}

.page-img {
  margin: 15px 15px 5px 15px;
  border-radius: 10px;
}

.page-img-desc {
  margin-bottom: 10px;
  font-weight: bold;
  font-size: 0.9rem;
}

.collapse-container {
  text-align: left;
  width: 85%;
  min-width: 300px;
  margin: 10px auto;
}

.collapse-title {
  position: relative;
  padding: 5px 10px;
  font-size: 1.2rem;
  width: 100%;
  min-height: 20px;
}

.collapse-title-text {
  display: inline-block;
  margin-left: 30px;
  max-width: calc(100% - 100px);
  word-wrap: break-word;
}

.collapse-content {
  width: 100%;
  background: aliceblue;
  padding: 10px 10px 10px 60px;
}

@media only screen and (max-width: 450px) {
  .nav-class {
    width: 100px;
    font-size: 1rem;
  }

  .nav-class p {
    margin-top: 12px;
  }

  .arrow-to,
  .arrow-from {
    width: 50px;
  }

  .task-img img{
    height: 100px;
  }
}
